import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import BootstrapTable from "react-bootstrap-table-next";
import { connect } from "react-redux";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import {
  getBandrol,
  hideModal,
  showModal,
  ShowModalBandrol,
} from "../../../actions/datamaster_action.jsx";
import FormBandrol from "./FormBandrol.jsx";
import {
  NotifError,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { postData, putData } from "../../../config/axios.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataBandrol: state.datamaster.getBandrol,
    hideModal: state.datamaster.modalDialog,
  };
};

class MasterBandrol extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      columns: [
        {
          dataField: "keterangan",
          text: "Keterangan ",
        },
        {
          dataField: "berat_bandrol",
          text: "Berat Bandrol ",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              keterangan: row.keterangan,
              berat_bandrol: row.berat_bandrol,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditDataKategori(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    // console.log(data);
    let data_edit = {
      berat_bandrol: data.berat_bandrol,
    };
    let data_post = {
      keterangan: data.keterangan,
      berat_bandrol: data.berat_bandrol,
    };
    this.state.isEdit
      ? putData("parabandrol/edit/by-keterangan/" + data.keterangan, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getBandrol());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("FormBandrol"));
          })
          .catch((error) => this.ErrorHandling(error))
      : postData("pababandrol/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getBandrol());
          })
          .then(() => {
            this.props.dispatch(reset("FormBandrol"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) => this.ErrorHandling(error));
  }
  ErrorHandling(err) {
    this.setState({ isLoading: false });
    NotifError(err);
  }
  componentDidMount() {
    // getData("parabandrol/get/all")
    //   .then((res) => {
    //     this.setState({
    //       listBandrol: res.data,
    //     });
    //   })
    //   .catch((err) => {
    //     console.log(err);
    //   });
    this.props.dispatch(getBandrol());
  }
  formEditDataKategori(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalBandrol(dataEdit));
    this.setState({
      isEdit: true,
    });
  }

  showModalKategori() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalBandrol(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Bandrol</li>
        </ol>
        <h1 className="page-header">Master Bandrol </h1>
        <Panel>
          <PanelHeader>Master Bandrol</PanelHeader>
          <br />
          <div className="container">
            <ToolkitProvider
              keyField="keterangan"
              data={this.props.DataBandrol || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Master Gudang.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6 mb-3">
                    {this.props.DataBandrol.length === 0 ? (
                      <button
                        onClick={() => this.showModalKategori()}
                        className="btn btn-primary"
                      >
                        <i className="fa fa-plus"></i>
                      </button>
                    ) : null}
                  </div>
                  <div className="col-12">
                    <BootstrapTable {...props.baseProps} />
                    <br />
                  </div>
                </div>
              )}
            </ToolkitProvider>
          </div>
          <br />
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit ? "Edit Data Bandrol" : "Tambah Data Bandrol"
            }
            content={
              <FormBandrol
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterBandrol);
