import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { Gramasi, ReanderField } from "../../../components/helpers/field";
import ValidasiMasterGudang from "../../../Validasi/ValidasiMasterGudang.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalBandrol !== null) {
    return {
      initialValues: {
        keterangan: state.datamaster.ShowModalBandrol.keterangan,
        berat_bandrol: state.datamaster.ShowModalBandrol.berat_bandrol,
      },
    };
  }
};
class FormBandrol extends Component {
  state = {
    aktif: false,
  };

  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("berat_bandrol").focus()
        : document.getElementById("keterangan").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          id="keterangan"
          tabIndex="1"
          name="keterangan"
          component={ReanderField}
          type="text"
          label="Keterangan"
          readOnly={this.props.isEdit}
          placeholder="Masukan Keterangan"
        />

        <Field
          tabIndex="2"
          id="berat_bandrol"
          name="berat_bandrol"
          component={Gramasi}
          type="text"
          label="Berat Bandrol"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Berat Bandrol"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormBandrol = reduxForm({
  form: "FormBandrol",
  enableReinitialize: true,
  validate: ValidasiMasterGudang,
})(FormBandrol);
export default connect(maptostate, null)(FormBandrol);
